*{
 
 margin: 0;
padding: 0;
 box-sizing: border-box;

}


body{

	font-family: 'montseratt',sans-serif;
}

.app{

background-image: url('./assets/cold.jpg');
background-size: cover;
background-position: bottom;
transition: 0.4 ease-out;

}
.app.warm {
	background-image: url('./assets/warm.jpg');
  }

main{
	min-height: 100vh;
padding:25px;
	background-image: linear-gradient(to bottom,rgba(0,0,0,0.2),rgba(0,0,0,0.75))
}

.search-box{

width:100%;
margin:0 0 75px;

}

.search-box .search-bar{

	display: block;
	width:100%;
	padding:15px;

	background: none;
	border: none;
	outline:none;
	appearance:none;

	background-color: rgba(255,255,255,0.5);
	border-radius: 0px 0px 16px 16px;
	margin-top: -25px;
	

	box-shadow: 0px,5px,rgba(0,0,0,0.2);

	color: #313131;
	font-size: 20px;

	transition: 0.4s ease;

}

.search-box .search-bar:focus{

background-color: rgba(255,255,255,0.75);

}

.location-box .location{
	color: #FFF;
	font-size: 32px;
	font-weight: 500;
	text-align: center;
	text-shadow: 3px 3px rgba;
	
}

.location-box .date{
	color: #FFF;
	font-size: 20px;
	font-weight: 300;
	font-style: italic;
	text-align: center;
}

.weather-box{
	text-align: center;
}

.weather-box .temp{

	position: relative;
	display: inline-block;
	margin: 30px auto;
	background-color: rgba(255,255,255,0.3);
	border-radius: 16px;
	
	padding: 15px 25px;

	color: #FFF;
	font-size: 102px;
	font-weight: 900;
	align-content: center;

	text-shadow: 3px 6px blur #000;
	text-align: center;
	box-shadow: 3px 6px rgba(0,0,0,0.4);
}

.weather-box .weather{
color: #FFF;
font-size: 48px;
font-weight: 600;
text-shadow: 3px 6px rgba(0,0,0,0.77);

}